.Search_{
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: row-reverse;
	justify-content: center;
	align-items: center;
}

.SearchButton{
	border:none;
	background-color: transparent;
	color: var(--Primary);
	font-size: 25px;
	transition: all 100ms linear;
	display: flex;
	flex-direction: row-reverse;
	justify-content: center;
	align-items: center;
}
.SearchButton:active{
	transform: scale(96%);
}


.SearchMain{
	margin-right: 10px;
	display: none;
}
.Inp_form{
	width: 100%;
	height: 100%;
}
.Input_Search{
    background-color: transparent;
    border: 0px solid;
    height: 20px;
    width: 160px;
    color: #CCC;
	outline: none;
}
.Input_Search:focus{
	outline: none;
}
