.Center_{
    margin-top: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Holder_{
    margin-top: 50px;
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto;
}
.AnimeCard{
    background-color: white;
    border: none;
    height: 275;
    width: 150px;
    margin: 10px;
    box-shadow: 5px 5px 10px rgba(0,0,0,0.5);
    border-radius: 10px;
    transition: all 100ms linear;
}
.AnimeIMG{
    width: 100%;
    height: 80%;
    border-radius: 10px;
    margin-top: 5px;
    padding: 0;
}
.Title{
    font-size: 10px;
    width: 100%;
    height: 100%;
    overflow: auto;
    word-wrap: break-word;

}
.AnimeCard:focus{
    box-shadow: 5px 5px 10px rgba(0,0,0,0.2);   
}
.AnimeCard:hover{
    box-shadow: 5px 5px 10px rgba(0,0,0,0.8);
}
.AnimeCard:active{
    transform: scale(96%);
    background-color: rgb(35,35,35);
    color: white;
}