:root{
    --Primary:#7188d3;
    --Secoundry:#2c2c2c;
}
.Nav{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 50px;
}
.NavBar_Nav{
    background-color: var(--Secoundry);
    width: 100%;
    height: 100%;
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
}
.Logo{
    margin-left: 10px ;
    margin-right: auto ;
    width: 50px;

}
.Logo img{
    width: 100%;
    height: 100%;
}
.Search_div{
    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: 10px;
}